const actions = {
  getAccountHistorySubLedgerReports: (page, search, filter, filter_select) => ({
    type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_REPORTS_REQUEST",
    payload: {
      search: search,
      filter: filter,
      filter_select
    }
  }),
  getSuppliersBasedOnLocation: id_location => ({
    type: "GET_SUPPLIERS_BASED_ON_LOCATION_REQUEST",
    payload: { id_location: id_location }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
