import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  Empty,
  DatePicker,
  Divider,
  Input,
  Row,
  Select,
  Spin,
  TreeSelect
} from "antd";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";

// import action
import actions from "../../../../redux/report/accountHistorySubLedgerReport/actions";
import { siteConfig } from "../../../../settings";
import { getFileName } from "../../../../helpers/helper";

const Option = Select.Option;
const Panel = Collapse.Panel;
const Search = Input.Search;
const RangePicker = DatePicker.RangePicker;

const loop = array =>
  array.map(data => {
    let perData;
    if (data.is_active === 0) {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        disabled: true
      };
    } else {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        children: data.children ? loop(data.children) : null,
        disabled: data.children.length > 0 ? true : false
      };
    }
    return perData;
  });

const redirectTo = "/dashboard/report-menus";

class AccountHistorySubLedgerReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null,
      sub_ledger_type: null,
      id_customer: null,
      id_supplier: null,
      id_account: null,
      sort_by: "transaction_date",
      sort_type: "asc"
    },
    search: null
  };

  componentDidMount() {
    const { getAccountHistorySubLedgerReports } = this.props;
    getAccountHistorySubLedgerReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.accountHistorySubLedger.error) {
      notification("error", this.props.accountHistorySubLedger.error);
      this.props.clearError();
    }
    // Display success notif
    if (this.props.accountHistorySubLedger.success) {
      notification("success", this.props.accountHistorySubLedger.success);
      this.props.clearSuccess();
    }
  }

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_account) {
      string
        ? (string += "&id_account=" + filter.id_account)
        : (string += "id_account=" + filter.id_account);
    }
    if (filter.sub_ledger_type) {
      string
        ? (string += "&sub_ledger_type=" + filter.sub_ledger_type)
        : (string += "sub_ledger_type=" + filter.sub_ledger_type);

      if (filter.sub_ledger_type === 1 && filter.id_supplier) {
        string
          ? (string += "&id_sub_ledger=" + filter.id_supplier)
          : (string += "id_sub_ledger=" + filter.id_supplier);
      } else if (filter.sub_ledger_type === 2 && filter.id_customer) {
        string
          ? (string += "&id_sub_ledger=" + filter.id_customer)
          : (string += "id_sub_ledger=" + filter.id_customer);
      }
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getAccountHistorySubLedgerReports } = this.props;
    let filter = this.filterValue();
    this.setState({ search: null });
    getAccountHistorySubLedgerReports(1, null, filter, this.state.filter);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      const { getSuppliersBasedOnLocation } = this.props;
      getSuppliersBasedOnLocation(value);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getAccountHistorySubLedgerReports } = this.props;
  //   const { search, filter } = this.props.accountHistorySubLedger;
  //   //console.log(search, filter, this.state.filter);
  //   getAccountHistorySubLedgerReports(page, search, filter, this.state.filter);
  // };
  handleSelectAccount = value => {
    const { accounts } = this.props.accountHistorySubLedger;
    let obj = null;
    const filter = array => {
      array.map(data => {
        if (data.children) {
          if (data.id_account === value) {
            obj = data;
          } else {
            filter(data.children);
          }
        } else {
          if (data.id_account === value) {
            obj = data;
          } else {
            obj = null;
          }
        }
      });
    };
    filter(accounts);
    obj = Object.assign({}, obj, { children: null });
    this.setState({
      filter: {
        ...this.state.filter,
        id_account: obj.id_account
      }
    });
  };

  // Handle action di table
  handleRecord = (action, id) => {
    // console.log(action, data);
    if (action === "activate") {
      // data.is_active = 1;
      this.props.activateSupplierPriceList(id);
    } else {
      // data.is_active = 0;
      this.props.deactivateSupplierPriceList(id);
    }
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAccountHistorySubLedgerReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null,
        sub_ledger_type: null,
        id_customer: null,
        id_supplier: null,
        id_account: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getAccountHistorySubLedgerReports();
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAccountHistorySubLedgerReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null,
        sub_ledger_type: null,
        id_customer: null,
        id_supplier: null,
        id_account: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getAccountHistorySubLedgerReports();
  };

  handleSearch = value => {
    const { getAccountHistorySubLedgerReports } = this.props;
    let filter = this.filterValue();
    getAccountHistorySubLedgerReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  renderTransTypeString = data => {
    let transaction_type_string;

    switch (data) {
      case 1:
        transaction_type_string = getLanguage({ id: "openingBalance" });
        break;
      case 2:
        transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
        break;
      case 3:
        transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
        break;
      case 4:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseReceipts"
        });
        break;
      case 5:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseInvoices"
        });
        break;
      case 6:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseReturns"
        });
        break;
      case 7:
        transaction_type_string = getLanguage({ id: "sidebar.itemTransfer" });
        break;
      case 8:
        transaction_type_string = getLanguage({ id: "sidebar.deliveryOrder" });
        break;
      case 9:
        transaction_type_string = getLanguage({ id: "sidebar.salesInvoice" });
        break;
      case 11:
        transaction_type_string = getLanguage({ id: "sidebar.salesReturn" });
        break;
      case 12:
        transaction_type_string = getLanguage({ id: "sidebar.fixedAsset" });
        break;
      case 13:
        transaction_type_string = getLanguage({ id: "journalVoucher" });
        break;
      case 14:
        transaction_type_string = getLanguage({
          id: "accountReceivablePayment"
        });
        break;
      case 15:
        transaction_type_string = getLanguage({ id: "accountPayablePayment" });
        break;
      case 16:
        transaction_type_string = getLanguage({ id: "creditMemo" });
        break;
      case 17:
        transaction_type_string = getLanguage({ id: "debitMemo" });
        break;
      case 18:
        transaction_type_string = getLanguage({ id: "sidebar.cashManagement" });
        break;
      case 19:
        transaction_type_string = getLanguage({ id: "sidebar.jobCosting" });
        break;
      case 20:
        transaction_type_string = getLanguage({
          id: "sidebar.jobCostRollover"
        });
        break;
      case 21:
        transaction_type_string = getLanguage({ id: "supplierDeposit" });
        break;
      case 22:
        transaction_type_string = getLanguage({ id: "customerDeposit" });
        break;
      case 23:
        transaction_type_string = getLanguage({
          id: "sidebar.fixedAssetRevaluation"
        });
        break;
      case 24:
        transaction_type_string = getLanguage({ id: "Raptor Sales Invoice" });
        break;
      case 25:
        transaction_type_string = getLanguage({ id: "Raptor Sales Return" });
        break;
      case 26:
        transaction_type_string = getLanguage({ id: "POS Sales Invoice" });
        break;
      case 27:
        transaction_type_string = getLanguage({ id: "POS Sales Return" });
        break;
      default:
        transaction_type_string = "-";
    }
    return transaction_type_string;
  };

  render() {
    const { accountHistorySubLedger } = this.props;
    const {
      accounts,
      //account_types,
      loadingSuppliers,
      loadingForeign,
      loading,
      locations,
      suppliers,
      customers,
      account_history_sub_ledger_reports,
      filter,
      filter_select
    } = accountHistorySubLedger;

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const excelMiniColumns = [
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 5
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type_string",
        rowKey: "transaction_type_string",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        rowKey: "account_code",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "12%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        align: "right",
        title: getLanguage({ id: "debit" }),
        dataIndex: "debit",
        rowKey: "debit",
        parse: true,
        width: "10%"
      },
      {
        align: "right",
        title: getLanguage({ id: "credit" }),
        dataIndex: "credit",
        rowKey: "credit",
        parse: true,
        width: "10%"
      }
    ];

    const columns = [
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        rowKey: "account_code",
        width: "8%"
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "13%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        render: text => {
          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        },
        width: "13%"
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type_string",
        rowKey: "transaction_type_string",
        render: text => {
          return <span>{text}</span>;
        },
        width: "11%"
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "15%"
      },
      {
        title: getLanguage({ id: "supplier/customer" }),
        dataIndex: "sub_ledger",
        rowKey: "sub_ledger",
        render: (text, row) => {
          if (row.sub_ledger_type === 1 && row.supplier) {
            return row.supplier.supplier_name;
          } else if (row.sub_ledger_type === 2 && row.customer) {
            return row.customer.customer_name;
          } else {
            return "-";
          }
        },
        width: "13%"
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "7%"
      },
      {
        title: getLanguage({ id: "currency" }),
        dataIndex: "currency.currency_code",
        rowKey: "currency.currency_code",
        width: "8%"
      },
      {
        align: "right",
        title: getLanguage({ id: "debit" }),
        dataIndex: "debit",
        rowKey: "debit",
        parse: true,
        render: (text, row) => {
          if (row.debit_credit === 1) {
            return (
              <span>
                <NumberFormat value={parseFloat(row.amount)} />
              </span>
            );
          } else {
            return "-";
          }
        }
      },
      {
        align: "right",
        title: getLanguage({ id: "credit" }),
        dataIndex: "credit",
        rowKey: "credit",
        parse: true,
        render: (text, row) => {
          if (row.debit_credit === 2) {
            return (
              <span>
                <NumberFormat value={parseFloat(row.amount)} />
              </span>
            );
          } else {
            return "-";
          }
        }
      }
    ];

    // console.log(accountHistorySubLedger)
    // --- init variable
    let excelDataSource = [];
    let dataSource = [];

    if (
      account_history_sub_ledger_reports &&
      account_history_sub_ledger_reports.length > 0
    ) {
      account_history_sub_ledger_reports.map((data, index) => {
        var transaction_type_string = this.renderTransTypeString(
          data.transaction_type
        );
        var debit = "";
        var credit = "";
        if (data.debit_credit === 1) {
          debit = data.amount;
        } else {
          credit = data.amount;
        }
        var sub_ledger = "-";
        if (data.sub_ledger_type === 1 && data.supplier) {
          sub_ledger = data.supplier.supplier_name;
        } else if (data.sub_ledger_type === 2 && data.customer) {
          sub_ledger = data.customer.customer_name;
        }
        excelDataSource.push({
          ...data,
          credit: credit,
          debit: debit,
          sub_ledger: sub_ledger,
          transaction_type_string: transaction_type_string
        });
        return dataSource.push({
          ...data,
          key: index,
          transaction_type_string: transaction_type_string
        });
      });
    }

    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //const { current_page, page_size, total } = this.props.accountHistorySubLedger;
    const fileName = getLanguage({
      id: "accountHistorySubLedgerReports.pageHeader"
    });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={
              <IntlMessages id="accountHistorySubLedgerReports.pageHeader" />
            }
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="accountHistorySubLedgerReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              margin={{
                top: ".7cm",
                bottom: ".7cm",
                left: ".5cm",
                right: ".6cm"
              }}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.5}
              title="accountHistorySubLedgerReports.pageHeader"
            />

            <ExcelWrapper
              title="accountHistorySubLedgerReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={`${getLanguage({
                id: "accountCode"
              })}, ${getLanguage({ id: "transactionNo" })}`}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>
            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          {/* Filter */}
          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.account" />
                    </label>
                    <br />
                    <TreeSelect
                      // allowClear={true}
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      loading={loadingForeign}
                      onSelect={this.handleSelectAccount}
                      placeholder={<IntlMessages id="accountName.select" />}
                      style={{ width: "95%" }}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      treeData={
                        accounts && accounts.length > 0 ? loop(accounts) : null
                      }
                      treeDefaultExpandAll
                      value={
                        this.state.filter.id_account
                          ? this.state.filter.id_account
                          : null
                      }
                    />
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      //allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="account_code">
                            <IntlMessages id="accountCode" />
                          </Option>
                          <Option key={2} value="account_name">
                            <IntlMessages id="accountName" />
                          </Option>
                          {/* <Option key={3} value="transaction_type">
                            <IntlMessages id="transactionType" />
                          </Option> */}
                          <Option key={4} value="transaction_date">
                            <IntlMessages id="transactionDate" />
                          </Option>
                          <Option key={5} value="transaction_no">
                            <IntlMessages id="transactionNo" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Col span={11}>
                      <label>
                        <IntlMessages id="subLedgerType" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "sub_ledger_type"
                        )}
                        value={
                          this.state.filter.sub_ledger_type
                            ? this.state.filter.sub_ledger_type
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={<IntlMessages id="subLedgerType" />}
                        optionFilterProp="children"
                        filterOption={true}
                        allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value={1}>
                          <IntlMessages id="suppliers.pageHeader" />
                        </Option>
                        <Option key={2} value={2}>
                          <IntlMessages id="customers.pageHeader" />
                        </Option>
                      </Select>
                    </Col>
                    {this.state.filter.sub_ledger_type ? (
                      this.state.filter.sub_ledger_type === 1 ? (
                        <Col span={13}>
                          <label>
                            <IntlMessages id="suppliers.pageHeader" />
                          </label>
                          <br />
                          <Select
                            showSearch
                            onChange={this.handleFilterChange.bind(
                              this,
                              "id_supplier"
                            )}
                            value={
                              this.state.filter.id_supplier
                                ? this.state.filter.id_supplier
                                : []
                            }
                            style={{ width: "90%" }}
                            notFoundContent={
                              loadingSuppliers ? (
                                <Spin size="small" />
                              ) : this.state.filter.id_location ? (
                                <Empty
                                  description={<IntlMessages id="noData" />}
                                />
                              ) : (
                                <Empty
                                  description={
                                    <IntlMessages id="itemRequests.item.noLocation" />
                                  }
                                />
                              )
                            }
                            placeholder={
                              this.state.filter.id_location ? (
                                <IntlMessages id="select.supplier" />
                              ) : (
                                <IntlMessages id="itemRequests.item.noLocation" />
                              )
                            }
                            optionFilterProp="children"
                            filterOption={true}
                            loading={loadingForeign}
                            allowClear={true}
                            //showArrow={false}
                          >
                            {this.state.filter.id_location
                              ? suppliers.map((data, key) => (
                                  <Option key={key} value={data.id_supplier}>
                                    {`${data.supplier_code} (${data.supplier_name})`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        </Col>
                      ) : this.state.filter.sub_ledger_type === 2 ? (
                        <Col span={13}>
                          <label>
                            <IntlMessages id="customers.pageHeader" />
                          </label>
                          <br />
                          <Select
                            showSearch
                            onChange={this.handleFilterChange.bind(
                              this,
                              "id_customer"
                            )}
                            value={
                              this.state.filter.id_customer
                                ? this.state.filter.id_customer
                                : []
                            }
                            style={{ width: "90%" }}
                            placeholder={<IntlMessages id="select.customer" />}
                            optionFilterProp="children"
                            filterOption={true}
                            loading={loadingForeign}
                            allowClear={true}
                            //showArrow={false}
                          >
                            {customers.map((data, key) => (
                              <Option key={key} value={data.id_customer}>
                                {`${data.customer_code} (${data.customer_name})`}
                              </Option>
                            ))}
                          </Select>
                        </Col>
                      ) : null
                    ) : null}
                  </Col>

                  <Col
                    span={4}
                    style={{
                      marginTop: "24px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          {/* /Filter */}

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="details" />
          </Divider>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : loading ? (
              <Spin></Spin>
            ) : !loading && dataSource.length === 0 ? (
              <Empty
                description={
                  <IntlMessages id="accountHistorySubLedgerReports.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    bordered={true}
                    dataSource={dataSource}
                    columns={columns}
                    className="invoiceListTable forceHeight"
                    loading={loading}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    accountHistorySubLedger: state.accountHistorySubLedgerReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  AccountHistorySubLedgerReport
);
